import { useEffect, useState } from "react";

//Our Imports
import toastDispacher   from "Helpers/toastDispacher";
import zip_codes_mx     from "Resources/data/zip_codes_mx/zip_codes_mx.json";
import AddressFormInpts from "./AdreessFormInpts";

const AddressFormInptsContainer = ({
	hookForm,
	inptsRules,
	zipCodeKey,
	inptsValues,
	municipalityKey,
	settlementTypeKey,
}) => {
	const [settlementOptions, setSettlementOptions] = useState([]);

	const watcherZipCode = hookForm.watch(zipCodeKey);

	const setZipCodeValues = () => {
		const { d_ciudad, ...localities } = zip_codes_mx[watcherZipCode];

		delete localities["d_estado"];
		delete localities["c_estado"];
		delete localities["c_mnpio"];

		const options = Object.entries(localities).map(([key, values]) => ({  label : key, value : values.id_asenta_cpcons, ...values }) );

		setSettlementOptions(options);

		hookForm.setValue(municipalityKey, d_ciudad);
		hookForm.clearErrors(municipalityKey);
	};


	useEffect(() => {
		if (watcherZipCode?.length !== 5) return;

		const zipCode = zip_codes_mx[watcherZipCode];

		if (!zipCode) {
			toastDispacher.zipCode.warning(watcherZipCode);

			setSettlementOptions([]);

			hookForm.setValue(settlementTypeKey, "");
			hookForm.setValue(municipalityKey, "");
			return;
		}
		setZipCodeValues();
	}, [watcherZipCode]);
	return (
		<AddressFormInpts
			delegations={{
				hookForm,
				zipCodeKey,
				inptsRules,
				inptsValues,
				settlementTypeKey,
				settlementOptions,
			}}
		/>
	);
};

export default AddressFormInptsContainer;
