
//Our Imports
import BtnsActions             from "Components/Global/BtnsActions";
import DropeZoneFile           from "Components/Global/DropeZoneFile";
import CreateRequestForm       from "Components/Request/CreateRequestForm";
import { FileMinus, FilePlus } from "react-feather";
import "./RequestCreatePage.scss";

const dropeZoneSize ={ width : "100%"};

const RequestCreatePage = ({
	delegations : {
		filesState,
		hookForm,
		onSubmit,
		isLoading,
		isEditing,
		inptsRules,
		isLine3Form,
		handleSelectChange,
		handleTertaryBtnClick,
		isFetchingRequestToEdit,
	},
}) => {
	return (
		<form onSubmit={onSubmit} id="RequestCreatePage">
			<div  className="wrapper">
				<CreateRequestForm
					hookForm={hookForm}
					isEditing={isEditing}
					inptsRules={inptsRules}
					isLine3Form={isLine3Form}
				/>
				<div className="rigth-column">
					<BtnsActions
						loading={isLoading || isFetchingRequestToEdit}
						primaryBtnLabel="Guardar"
						primaryBtnIcon={<FilePlus size={16} />}
						handlePrimaryBtn={onSubmit}
						showSecundaryBtn={false}
						tertiaryBtnLabel="Cancelar"
						tertiaryBtnIcon={<FileMinus size={16} />}
						handletertiaryBtn={handleTertaryBtnClick}
					/>
					<div>
						<DropeZoneFile
							label="Escritura"
							customStylesText="dropezone-text"
							defaultFile={filesState?.["deed"]}
							errors={hookForm.formState.errors?.files?.deed}
							setValue={handleSelectChange("deed")}
							{...dropeZoneSize}
						/>
						<DropeZoneFile
							label="Plano"
							customStylesText="dropezone-text"
							defaultFile={filesState?.["plane"]}
							errors={hookForm.formState.errors?.files?.plane}
							setValue={handleSelectChange("plane")}
							{...dropeZoneSize}
						/>
						<DropeZoneFile
							label="Boleta Predial"
							customStylesText="dropezone-text"
							defaultFile={filesState?.["property_tax_bill"]}
							errors={hookForm.formState.errors?.files?.property_tax_bill}
							setValue={handleSelectChange("property_tax_bill")}
							{...dropeZoneSize}
						/>
						<DropeZoneFile
							label="Boleta de Agua"
							customStylesText="dropezone-text"
							defaultFile={filesState?.["water_bill"]}
							errors={hookForm.formState.errors?.files?.water_bill}
							setValue={handleSelectChange("water_bill")}
							{...dropeZoneSize}
						/>
						<DropeZoneFile
							maxFiles={5}
							className="mb-4"
							customStylesText="dropezone-text"
							defaultFile={filesState?.["other_files"]}
							label="Otros Archivos (Máximo 5 archivos)"
							acceptFiles={{ image : true, xlsx : true, docs : true, pdf : true }}
							setValue={handleSelectChange("other_files")}
							{...dropeZoneSize}
						/>
					</div>
				</div>
			</div>
		</form>
	);
};

export default RequestCreatePage;
