import {
	useRef,
	useState,
	useEffect,
} from "react";
import { connect }               from "react-redux";
import { useForm }               from "react-hook-form";
import { useHistory, useParams } from "react-router-dom";

//Our Imports
import { bindAll, isValidArray } from "Helpers";

import {
	useQuery,
	useSendFiles,
	useHandlerMutation,
} from "Helpers/Hooks";

import {
	levelOptions,
	livingPlaceOptions,
	roadTypeOptions,
	roomOptions,
	settlementTypeOptions,
	typologyOptions,
	zoneTypeOptions,
} from "Resources/data/RequestForm";

import { RequestSlice }  from "Store/Slices";
import { genericApi  }   from "Store/api/genericApi";
import RequestCreatePage from "./RequestCreatePage";


const RequestCreateContainerPage = ({
	formErros,
	requestSliceActions,
}) => {

	const query   = useQuery();
	const { id }  = useParams();
	const history = useHistory();

	const isEditing   = !(id === "id");
	const isLine3Form = query.get("type") > 0;

	const [filesState, setFilesState]         = useState({});
	const [isLoadingFiles, setIsLoadingFiles] = useState(false);
	const [inptsRules, setInptsRules]         = useState({
		required : {
			value   : false,
			message : "Este campo no puede estar vacío.",
		},
	});

	const prevIdPlace     = useRef(null);
	const controllerInpts = useRef({
		"deed"              : false,
		"plane"             : false,
		"property_tax_bill" : false,
		"water_bill"        : false,
	});

	const [ fetchKey ] = genericApi.useLazyGetDataQuery();
	const [
		fetchRequestToEdit,
		{ isFetching : isFetchingRequestToEdit },
	] = genericApi.useLazyGetDataQuery();

	const [ createRequest, { isLoading, isSuccess : requestSuccess } ] = useHandlerMutation({
		toastName : "requestCRUD",
		options   : { succesParam : isEditing },
	});

	const  [ handleSubmitFiles, handleRemoveFiles ] = useSendFiles({
		module : "petition",
		tags   : ["petitions", "petition_details"],
	});

	const { reset, handleSubmit, ...hookForm } = useForm({ shouldUnregister : true });

	const handleSelectChange = (key) => (val) => {
		setFilesState({ ...filesState, [key] : val });
		hookForm.clearErrors(`files.${key}`);
	};

	const handleTertaryBtnClick = () => history.replace("/app/request");

	const handleSetError = (key, errorMsg = "Este campo no puede estar vacío.") => {
		if (!filesState?.[key]) {
			hookForm.setError(`files.${key}`, {
				type    : "manual",
				message : errorMsg,
			});
		}
	};

	const handleValidateControllerInpts = () => {
		for (const [key, value] of Object.entries(controllerInpts.current)) {
			if (value) {
				handleSetError(key);
			}
		}
	};

	const onSubmit = (...args) => {

		handleValidateControllerInpts();

		handleSubmit( async (data) => {
			let key;
			if (isEditing) {
				const { id_place } = data;

				key = prevIdPlace.current._id === id_place.value ? hookForm.getValues("key")
					: await fetchKey({ module : "petitions/key", params : { code : data.id_place.alias }, tags : [] }).unwrap();
			} else {
				key = await fetchKey({ module : "petitions/key", params : { code : data.id_place.alias }, tags : [] }).unwrap();
			}

			const formData = { ...data };

			const petition_type =  isEditing ? hookForm.getValues("petition_type") : (Number(query.get("type")) ?? 0);

			const housing = {
				...formData.housing,
				settlement            : formData.housing.settlement?.label  ?? undefined,
				id_place              : formData.housing?.id_place?.value   ?? undefined,
				house_type            : formData.housing?.house_type?.value ?? undefined,
				settlement_type       : formData.housing?.settlement_type?.value ?? undefined,
				street_type           : formData.housing?.street_type?.value ?? undefined,
				zone_type             : formData.housing?.zone_type?.value   ?? undefined,
				street_between_1_type : formData.housing?.street_between_1_type?.value ?? undefined,
				street_between_2_type : formData.housing?.street_between_2_type?.value ?? undefined,
				street_back_type   	  : formData.housing?.street_back_type?.value ?? undefined,
				number_rooms          : formData.housing?.number_rooms?.value ?? undefined,
				number_bathrooms   	  : formData.housing?.number_bathrooms?.value ?? undefined,
				parking_lot           : formData.housing?.parking_lot?.value ?? undefined,
				typology              : formData.housing?.typology?.value ?? undefined,
				level                 : formData.housing?.level?.value ?? undefined,
			};

			const dataToSubmit = {
				key : key?.data ?? key,
				petition_type,
				...formData,

				//Selects values
				id_valuer     : formData.id_valuer.value,
				id_controller : formData.id_controller.value,
				id_place      : formData.id_place.value,
				housing_type  : formData.housing_type?.value,

				applicant : {
					...formData.applicant,
					address : {
						...formData.applicant.address,
						colony : formData.applicant.address.colony?.label ?? undefined,
					},
				},

				owner : {
					...formData.owner,
					address : {
						...formData.owner.address,
						colony : formData.owner.address.colony?.label ?? undefined,
					},
				},

				housing,
			};

			const method = isEditing ? "PUT" : "POST";
			const module = isEditing ?  `petitions/${id}` : "petitions";

			const { data : response } = await createRequest({
				module,
				method,
				data : dataToSubmit,
				tags : ["petitions", "petition_details"],
			}).unwrap();

			if (response?._id) {
				setIsLoadingFiles(true);

				if (isEditing) {
					handleRemoveFiles();
				}
				await handleSubmitFiles(filesState, response._id);
			}
			setIsLoadingFiles(false);

		})(...args);
	};

	const getSelecteValue = (item) => {
		return item ? {
			label : item?.name,
			value : item?._id,
		} : undefined;
	};

	const setDefaultValues = async () => {
		if (!isEditing) return;

		const { data : requestToEdit} = await fetchRequestToEdit({ module : `petitions/${id}`, tags : [] }).unwrap();

		prevIdPlace.current = { ...requestToEdit.place };

		const isRequired  =  requestToEdit.status > 0;

		controllerInpts.current = {
			"deed"              : isRequired,
			"plane"             : isRequired,
			"property_tax_bill" : isRequired,
			"water_bill"        : isLine3Form && isRequired,
		};

		const rules = {
			required : {
				value   : isRequired,
				message : "Este campo no puede estar vacío.",
			},
		};

		const defaultValues = {
			...(requestToEdit || {}),

			id_controller : getSelecteValue(requestToEdit?.controller),
			id_valuer     : getSelecteValue(requestToEdit?.valuer),
			id_place      : getSelecteValue(requestToEdit?.place),

			owner : {
				...requestToEdit?.owner,
				address : {
					...requestToEdit?.owner?.address,
					colony : requestToEdit?.owner?.address?.colony ? { label : requestToEdit?.owner?.address?.colony } : undefined,
				},
			},

			applicant : {
				...requestToEdit?.applicant,
				address : {
					...requestToEdit?.applicant?.address,
					colony : requestToEdit?.applicant?.address?.colony ? { label : requestToEdit?.applicant?.address?.colony } : undefined,
				},
			},

			...(!isLine3Form ?
				{
					housing_type : typeof requestToEdit?.housing_type === "number" ?
						{
							label : requestToEdit.housing_type < 1 ? "Vivienda Nueva" : "Vivienda Usada",
							value : requestToEdit.housing_type,
						} : undefined,
				} : {}
			),

			housing : {
				...requestToEdit?.housing,
				settlement : requestToEdit?.housing?.settlement ? { label : requestToEdit?.housing?.settlement } : undefined,
				house_type : livingPlaceOptions?.[requestToEdit?.housing?.house_type] || undefined,
				id_place   : requestToEdit?.housing?.id_place?.value ? requestToEdit?.housing?.id_place : undefined,

				settlement_type       : settlementTypeOptions?.[requestToEdit.housing?.settlement_type] ?? undefined,
				street_type           : roadTypeOptions?.[requestToEdit.housing?.street_type] ?? undefined,
				zone_type             : zoneTypeOptions?.[requestToEdit.housing?.zone_type]   ?? undefined,
				street_between_1_type : roadTypeOptions?.[requestToEdit.housing?.street_between_1_type] ?? undefined,
				street_between_2_type : roadTypeOptions?.[requestToEdit.housing?.street_between_2_type] ?? undefined,
				street_back_type   	  : roadTypeOptions?.[requestToEdit.housing?.street_back_type] ?? undefined,
				number_rooms          : roomOptions?.[requestToEdit.housing?.number_rooms] ?? undefined,
				number_bathrooms   	  : roomOptions?.[requestToEdit.housing?.number_bathrooms] ?? undefined,
				parking_lot           : roomOptions?.[requestToEdit.housing?.parking_lot] ?? undefined,
				typology              : typologyOptions?.[requestToEdit.housing?.typology] ?? undefined,
				level                 : levelOptions?.[requestToEdit.housing?.level] ?? undefined,
			},
		};

		setFilesState(
			isValidArray(requestToEdit?.files) ?
				requestToEdit?.files.reduce((prev, file) => {
					return {
						...prev,
						[file.description] :
					file.description === "other_files" ?
						[
							...(isValidArray(prev[file.description])
								? prev[file.description] : []),
							{ ...file, path : file.name },
						]
						:
						{ ...file, path : file.name },
					};
				}, {})
				: {}
		);

		delete defaultValues?.controller;
		delete defaultValues?.valuer;
		delete defaultValues?.place;

		reset(defaultValues);
		handleSetErrors();
		setInptsRules(rules);
	};

	const handleSetErrors = async () => {
		if (formErros) {
			Object.keys(formErros).forEach(key => {
				hookForm.setError(key, { type : "custom", message : "Este campo no puede estar vacío." });
			});
		}
	};

	useEffect(() => {
		if (requestSuccess && !isLoadingFiles) {
			history.replace("/app/request");
		}
	}, [requestSuccess, isLoadingFiles]);

	useEffect(() => {
		setDefaultValues();
		return () => requestSliceActions.clearFormErrors();
	}, [isEditing]);

	useEffect(() => {
		let subscription;
		if (formErros) {
			 subscription = hookForm.watch((value, { name, type }) => {
				if (type === "change") {
					hookForm.trigger(name);
				}
			});
		}
		return () => {
			if (subscription && typeof subscription.unsubscribe === "function") {
				subscription.unsubscribe();
			}
		};
	  }, [hookForm.watch]);

	return (
		<RequestCreatePage
			delegations={{
				hookForm,
				onSubmit,
				filesState,
				isEditing,
				inptsRules,
				isLine3Form,
				handleSelectChange,
				handleTertaryBtnClick,
				isFetchingRequestToEdit,
				isLoading : (isLoading || isLoadingFiles),
			}}
		/>
	);
};

const mapDispatchToState = ({ _requestSlice }) => ({
	formErros : _requestSlice?.formErros ?? null,
});

const mapDispatchToProps = bindAll({
	RequestSliceActions : RequestSlice.actions,
});

export default connect(mapDispatchToState, mapDispatchToProps)(RequestCreateContainerPage);
