import { Mail } from "react-feather";

import { FormInputGroup } from "Components/Global";

import AddressFormInpts from "../AddressFormInpts";

const addresOwnerSection = [
	{name : "owner.address.street",       label : "Calle", placeholder : "Av. de las Americas"},
	{name : "owner.address.outer_number", label : "No. Ext", placeholder : "123"},
	{name : "owner.address.inner_number", label : "No. Interior (Opcional)", placeholder : "123"},
	{name : "owner.address.zip_code",     label : "C.P.", placeholder : "12345"},
	{name : "owner.address.colony",       label : "Colonia", placeholder : "Selecciona una colonia"},
	{name : "owner.address.municipality", label : "Delegación o Municipio", placeholder : "Delegación o Municipio"},
];

const OwnerFormSections = ({ hookForm, inptsRules, isLine3Form }) => {

	return (
		<div className="w-100">
			<h1 className="title">Propetario</h1>
			<FormInputGroup
				name="owner.name"
				label={!isLine3Form ? "Nombre(s)" : "Nombre / Razón Social"}
				register={hookForm.register}
				errors={hookForm.formState.errors}
				placeholder={!isLine3Form ? "Ismael" : "Nombre / Razón Social"}
				options={inptsRules}
				style={{ textTransform : "uppercase" }}
			/>
			<FormInputGroup
				name="owner.rfc"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				label="RFC"
				placeholder="ROVI123456123"
				style={{ textTransform : "uppercase" }}
				options={{
					...inptsRules,
					pattern : {
						value   : /^[A-Z]{4}[0-9]{6}[A-Z0-9]{3}$/i,
						message : "RFC inválido",
					},
				}}
			/>
			<FormInputGroup
				name="owner.curp"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				label="CURP"
				style={{ textTransform : "uppercase" }}
				placeholder="ROVI490617HSPDSS05"
				options={{
					...inptsRules,
					pattern : {
						value   : /^[A-Z]{4}[0-9]{6}[H|M]{1}[A-Z]{5}[0-9]{2}$/i,
						message : "CURP inválido",
					},
				}}
			/>
			{
				isLine3Form &&
				<FormInputGroup
					name="owner.legal_agent"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					label="Representante Legal"
					placeholder="Representante Legal"
					style={{ textTransform : "uppercase" }}
					options={inptsRules}
				/>
			}
			<AddressFormInpts
				zipCodeKey="owner.address.zip_code"
				municipalityKey="owner.address.municipality"
				settlementTypeKey="owner.address.colony"
				hookForm={hookForm}
				inptsRules={inptsRules}
				inptsValues={addresOwnerSection}
			/>
			<FormInputGroup
				name="owner.email"
				icon={<Mail size={12} />}
				register={hookForm.register}
				errors={hookForm.formState.errors}
				label="Correo Electrónico"
				placeholder="email@email.com"
				options={{
					...inptsRules,
					pattern : {
						value   : /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
						message : "Correo electrónico inválido",
					},
				}}
			/>
		</div>
	);
};

export default OwnerFormSections;
