
//Our Importations
import { useFetchSelectOptions, useQuery } from "Helpers/Hooks";
import CreateRequestForm                   from "./CreateRequestForm";

const CreateRequestFormContainer = ({ hookForm, ...rest }) => {
	const query = useQuery();

	const placesOptions = useFetchSelectOptions("places");


	const watcherHousingType = hookForm.watch("housing_type");

	return (
		<CreateRequestForm
			delegations={{
				query,
				hookForm,
				placesOptions,
				watcherHousingType,
				...rest,
			}}
		/>
	);
};

export default CreateRequestFormContainer;
