import { useEffect } from "react";
import { connect }   from "react-redux";

//Our Imports
import { useFetchSelectOptions } from "Helpers/Hooks";
import HeaderFormInpts           from "./HeaderFormInpts.jsx";

const HeaderFormInptsContainer = ({
	_id,
	name,
	type_shf,
	hookForm,
	isEditing,
	isLine3Form,
}) => {

	const showHousingType = !isLine3Form;

	const placesOptions = useFetchSelectOptions("places");

	useEffect(() => {
		if (type_shf) {
			hookForm.setValue(
				type_shf === "valuer" ? "id_valuer" : "id_controller",
				{
					value : _id,
					label : name,
				}
			);
		}
	}, [type_shf]);


	return (
		<HeaderFormInpts
			delegations={{
				hookForm,
				isEditing,
				placesOptions,
				showHousingType,
			}}
		/>
	);
};
const mapDispatchToState = ({ _authSlice }) => ({
	_id      : _authSlice?.user?._id           ?? "",
	name     : _authSlice?.user?.name         ?? "",
	type_shf : _authSlice?.user?.type_shf ?? "",
});

export default connect(mapDispatchToState)(HeaderFormInptsContainer);
