import { connect } from "react-redux";

//Our Importations
import FormSelectGroup      from "Components/Global/FormSelectGroup";
import FormAsyncSelectGroup from "Components/Global/FormAsyncSelectGroup";


const livingTypeOptions = [
	{
		label : "Vivienda Nueva",
		value : 0,
	},
	{
		label : "Vivienda Usada",
		value : 1,
	},
];
const HeaderFormInpts = ({
	type_shf,
	delegations : {
		hookForm,
		placesOptions,
		showHousingType,
	},
}) => {
	return (
		<div className="header-inpts">
			<FormAsyncSelectGroup
				size="sm"
				module="users"
				name="id_valuer"
				placeholder="Valuador"
				query={{  typeShf : "valuer" }}
				isDisabled={type_shf === "valuer"}
				hookForm={hookForm}
			/>
			<FormAsyncSelectGroup
				size="sm"
				module="users"
				name="id_controller"
				query={{  typeShf : "controller" }}
				isDisabled={type_shf === "controller"}
				placeholder="Controlador"
				hookForm={hookForm}
			/>
			{
				showHousingType &&
				<FormSelectGroup
					size="sm"
					name="housing_type"
					placeholder="Tipo de vivienda"
					options={livingTypeOptions}
					hookForm={hookForm}
				/>
			}
			<FormSelectGroup
				size="sm"
				name="id_place"
				placeholder="Entidad Federativa"
				options={placesOptions}
				hookForm={hookForm}
			/>
		</div>
	);
};

const mapDispatchToState = ({ _authSlice }) => ({
	type_shf : _authSlice?.user?.type_shf ?? "",
});

export default connect(mapDispatchToState)(HeaderFormInpts);
