import FormInputGroup  from "Components/Global/FormInputGroup";
import FormSelectGroup from "Components/Global/FormSelectGroup";

const Line3Form = ({
	delegations : {
		hookForm,
		inptsRules,
		placesOptions,
	},
}) => {
	return (
		<>
			<h1 className="title">Vivienda</h1>
			<div className="mt-1">
				<FormInputGroup
					name="housing.street_name"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					label="Calle"
					placeholder="Av. de las Americas"
					options={inptsRules}
					style={{ textTransform : "uppercase" }}
				/>
				<div className="inpts-pairs">
					<FormInputGroup
						name="housing.official_number"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						label="No. Oficial"
						placeholder="123"
						options={inptsRules}
						style={{ textTransform : "uppercase" }}
					/>
					<FormInputGroup
						name="housing.street_between_1_name"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						label="Entre calle 1"
						placeholder="Juan de la Cruz"
						options={inptsRules}
						style={{ textTransform : "uppercase" }}
					/>
					<FormInputGroup
						name="housing.street_between_2_name"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						label="Entre calle 2"
						placeholder="Juarez"
						options={inptsRules}
						style={{ textTransform : "uppercase" }}
					/>
					<FormInputGroup
						name="housing.colony"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						label="Colonia"
						placeholder="Rincon de los Reyes"
						options={inptsRules}
						style={{ textTransform : "uppercase" }}
					/>
					<FormInputGroup
						maxLength="5"
						name="housing.zip_code"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						label="C.P."
						placeholder="45987"
						options={inptsRules}
						style={{ textTransform : "uppercase" }}
					/>
					<FormInputGroup
						name="housing.municipality"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						label="Delegación o Municipio"
						placeholder="Zapopan"
						style={{ textTransform : "uppercase" }}
						options={inptsRules}
					/>

					<FormSelectGroup
						name="housing.id_place"
						label="Entidad Federativa"
						placeholder="Selecciona una Entidad Federativa"
						options={placesOptions}
						hookForm={hookForm}
						rules={inptsRules}
					/>
					<FormInputGroup
						name="housing.sub_section"
						label="Manzana"
						placeholder="Manzana"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={inptsRules}
						style={{ textTransform : "uppercase" }}
					/>
					<FormInputGroup
						name="housing.lot"
						label="Lote"
						placeholder="Lote"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={inptsRules}
						style={{ textTransform : "uppercase" }}
					/>
					<FormInputGroup
						name="housing.section"
						label="Super Manzana"
						placeholder="Super Manzana"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={inptsRules}
						style={{ textTransform : "uppercase" }}
					/>
					<FormInputGroup
						name="housing.lat"
						label="Latitud"
						placeholder="192.52"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={inptsRules}
						style={{ textTransform : "uppercase" }}
					/>
					<FormInputGroup
						name="housing.lng"
						label="Longitud"
						placeholder="432.52"
						register={hookForm.register}
						errors={hookForm.formState.errors}
						options={inptsRules}
						style={{ textTransform : "uppercase" }}
					/>
				</div>
			</div>
		</>
	);
};

export default Line3Form;
