import { isValidArray } from "Helpers";
import FormCreatable    from "Components/Global/FormCreatable";
import FormInputGroup   from "Components/Global/FormInputGroup";

const AddressFormInpts = ({
	delegations: {
		hookForm,
		zipCodeKey,
		inptsRules,
		inptsValues,
		settlementTypeKey,
		settlementOptions,
	},
}) => {

	return (
		<>
			<h5 className="sub-title">Dirección</h5>
			<div  className="inpts-pairs">
				{
					isValidArray(inptsValues) &&
					inptsValues.map( ({name, label, placeholder = ""}) => (
						settlementTypeKey !== name ? (
							<FormInputGroup
								maxLength={name === zipCodeKey ? 5 : undefined}
								key={name}
								name={name}
								register={hookForm.register}
								errors={hookForm.formState.errors}
								placeholder={placeholder}
								label={label}
								options={inptsRules}
								style={{ textTransform : "uppercase" }}
							/>
						) : (
							<FormCreatable
								key={name}
								name={name}
								placeholder={placeholder}
								label={label}
								options={settlementOptions}
								hookForm={hookForm}
								errors={hookForm.formState.errors}
								menuPortalTarget={document.body}
								rules={inptsRules}
							/>
						)
					))
				}
			</div>
		</>
	);
};

export default AddressFormInpts;
