
//Our Imports
import FormInputGroup from "Components/Global/FormInputGroup";

const LivingPlaceUsedForm = ({ hookForm, inptsRules }) => {
	return (
		<div className="mt-1">
			<h5 className="sub-title">Datos de Escritura</h5>
			<FormInputGroup
				type="number"
				name="housing.notary_number"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				label="Número de Notario"
				placeholder="123456"
				options={inptsRules}
				style={{ textTransform : "uppercase" }}
			/>
			<FormInputGroup
				name="housing.notary_name"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				placeholder="Pedro González Ortega"
				label="Nombre del Notario"
				options={inptsRules}
				style={{ textTransform : "uppercase" }}
			/>
			<div className="inpts-pairs">
				<FormInputGroup
					name="housing.place"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					label="Plaza"
					placeholder="Plaza"
					options={inptsRules}
					style={{ textTransform : "uppercase" }}
				/>
				<FormInputGroup
					type="number"
					name="housing.deed_number"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					label="No. de Escritura"
					placeholder="No. de Escritura"
					options={inptsRules}
					style={{ textTransform : "uppercase" }}
				/>
				<FormInputGroup
					name="housing.book"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					label="Libro"
					placeholder="Libro"
					style={{ textTransform : "uppercase" }}
					options={inptsRules}
				/>
				<FormInputGroup
					name="housing.volume_book"
					register={hookForm.register}
					errors={hookForm.formState.errors}
					label="Volumen"
					placeholder="Volumen"
					options={inptsRules}
					style={{ textTransform : "uppercase" }}
				/>
			</div>
		</div>
	);
};

export default LivingPlaceUsedForm;
