import FormInputGroup   from "Components/Global/FormInputGroup";
import AddressFormInpts from "../AddressFormInpts";

import { useState, useEffect } from "react";

const addresApplicantSection = [
	{name : "applicant.address.street",       label : "Calle",  placeholder : "Av. de las Americas"},
	{name : "applicant.address.outer_number", label : "No. Ext", placeholder : "123"},
	{name : "applicant.address.inner_number", label : "No. Interior (Opcional)", placeholder : "Apto. 123"},
	{name : "applicant.address.zip_code",     label : "C.P.", placeholder : "12345"},
	{name : "applicant.address.colony",       label : "Colonia", placeholder : "Selecciona una colonia"},
	{name : "applicant.address.municipality", label : "Delegación o Municipio", placeholder : "Centro"},
];

const ApplicantFormSection = ({ hookForm, inptsRules}) => {

	const [isChecked, setIsChecked] = useState(false);
	const { watch } = hookForm;
	const streetWatcher = watch(addresApplicantSection[0].name);
	const outerWatcher = watch(addresApplicantSection[1].name);
	const innerWatcher = watch(addresApplicantSection[2].name);
	const zipWatcher = watch(addresApplicantSection[3].name);
	const colonyWatcher = watch(addresApplicantSection[4].name);
	const municipalityWatcher = watch(addresApplicantSection[5].name);

	const handleIsChecked = (value) => {
		setIsChecked(value);

		if (value) {
			addresApplicantSection.forEach((item) => {
				const fieldName = item.name.split(".")[2];
				const newval = hookForm.getValues(item.name);

				hookForm.setValue(`owner.address.${fieldName}`, newval);
			});
		}
	};

	useEffect(() => {
		if (isChecked) {
			addresApplicantSection.forEach((item) => {
				const fieldName = item.name.split(".")[2];
				const newval = hookForm.getValues(item.name);

				hookForm.setValue(`owner.address.${fieldName}`, newval);
			});
		}
	}, [streetWatcher, outerWatcher, innerWatcher, zipWatcher, colonyWatcher, municipalityWatcher]);

	return (
		<div className="w-100">
			<h1 className="title">Solicitante</h1>
			<FormInputGroup
				name="applicant.paternal_surname"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				label="Apellido Paterno"
				placeholder="Rodríguez"
				options={inptsRules}
				style={{ textTransform : "uppercase" }}
			/>
			<FormInputGroup
				name="applicant.maternal_surname"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				label="Apellido Materno"
				placeholder="Vásquez"
				options={inptsRules}
				style={{ textTransform : "uppercase" }}
			/>
			<FormInputGroup
				name="applicant.name"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				label="Nombre(s)"
				placeholder="Ismael"
				options={inptsRules}
				style={{ textTransform : "uppercase" }}
			/>
			<FormInputGroup
				name="applicant.rfc"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				label="RFC"
				placeholder="ROVI123456123"
				style={{ textTransform : "uppercase" }}
				options={{
					...inptsRules,
					pattern : {
						value   : /^[A-Z]{4}[0-9]{6}[A-Z0-9]{3}$/i,
						message : "RFC inválido",
					},
				}}
			/>
			<FormInputGroup
				name="applicant.nss"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				label="NSS"
				placeholder="12345678910"
				style={{ textTransform : "uppercase" }}
				options={{
					...inptsRules,
					pattern : {
						value   : /^[0-9]{11}$/,
						message : "NSS inválido, debe contener exactamente 11 dígitos numéricos",
					},
				}}
			/>
			<FormInputGroup
				name="applicant.curp"
				register={hookForm.register}
				errors={hookForm.formState.errors}
				label="CURP"
				placeholder="ROVI490617HSPDSS05"
				style={{ textTransform : "uppercase" }}
				options={{
					...inptsRules,
					pattern : {
						value   : /^[A-Z]{4}[0-9]{6}[H|M]{1}[A-Z]{5}[0-9]{2}$/i,
						message : "CURP inválido",
					},
				}}
			/>
			<AddressFormInpts
				zipCodeKey="applicant.address.zip_code"
				municipalityKey="applicant.address.municipality"
				settlementTypeKey="applicant.address.colony"
				hookForm={hookForm}
				inptsRules={inptsRules}
				inptsValues={addresApplicantSection}
			/>
			<div className="d-flex mb-2 gap-1 cursor-pointer pointer" onClick={() => handleIsChecked(!isChecked)}>
				<input type="checkbox" className="cursor-pointer" checked={isChecked} />
				<p style={{ userSelect : "none" }}>La dirección del Propietario es la misma que la del Solicitante.</p>
			</div>
		</div>
	);
};

export default ApplicantFormSection;
