import { useEffect, useState } from "react";

//Our Imports
import { useFetchSelectOptions } from "Helpers/Hooks";
import toastDispacher            from "Helpers/toastDispacher";
import { settlementTypeOptions } from "Resources/data/RequestForm";
import zip_codes_mx              from "Resources/data/zip_codes_mx/zip_codes_mx.json";
import LivingPlaceFormSection    from "./LivingPlaceFormSection.jsx";


const LivingPlaceFormSectionContainer = ({ type, hookForm, inptsRules }) => {
	const [settlementOptions, setSettlementOptions] = useState([]);

	const placesOptions = useFetchSelectOptions("places");

	const watcherSettlemnt = hookForm.watch("housing.settlement");
	const watcherZipCode   = hookForm.watch("housing.zip_code");

	const setZipCodeValues = () => {
		const { d_estado, d_ciudad, ...localities } = zip_codes_mx[watcherZipCode];
		delete localities["d_estado"];
		delete localities["c_estado"];
		delete localities["c_mnpio"];

		const options = Object.entries(localities).map(([key, values]) => ({  label : key, value : values.id_asenta_cpcons, ...values }) );

		setSettlementOptions(options);

		const state = placesOptions.find(place => place.label === d_estado);

		hookForm.setValue("housing.id_place", state);
		hookForm.setValue("housing.municipality", d_ciudad);

		hookForm.clearErrors("housing.id_place");
		hookForm.clearErrors("housing.municipality");

		console.log(hookForm.getValues("housing.settlement")?.label);

		const index = options.findIndex(option => option.label ===  hookForm.getValues("housing.settlement")?.label);

		if (index === -1) {
			hookForm.setValue("housing.settlement", "");
			hookForm.setValue("housing.settlement_type", "");
		}
	};

	const setSettlement_typeValues = () => {
		const { d_asenta, d_tipo_asenta } = hookForm.getValues("housing.settlement");

		if (!d_asenta && !d_tipo_asenta) return;

		const upperCase = d_tipo_asenta.toUpperCase();

		const settlement_type =  settlementTypeOptions.filter(option => option.label === upperCase);

		hookForm.setValue("housing.settlement_type", settlement_type?.[0] ?? undefined);

		hookForm.clearErrors("housing.settlement_type");
	};
	useEffect(() => {
		if (!watcherSettlemnt) return;

		setSettlement_typeValues();
	}, [watcherSettlemnt]);

	useEffect(() => {
		if (watcherZipCode?.length !== 5) return;

		const zipCode = zip_codes_mx[watcherZipCode];

		if (!zipCode) {
			toastDispacher.zipCode.warning(watcherZipCode);

			hookForm.setValue("housing.id_place", "");
			hookForm.setValue("housing.municipality", "");

			hookForm.setValue("housing.settlement", "");
			hookForm.setValue("housing.town", "");
			hookForm.setValue("housing.settlement_type", "");
			setSettlementOptions([]);
			return;
		}

		setZipCodeValues();
	}, [watcherZipCode]);

	return (
		<LivingPlaceFormSection
			delegations={{
				type,
				hookForm,
				inptsRules,
				placesOptions,
				settlementOptions,
			}}
		/>
	);
};

export default LivingPlaceFormSectionContainer;
