
//Our Imports
import Line3Form              from "../Line3Form";
import HeaderFormInpts        from "../HeaderFormInpts";
import OwnerFormSection       from "../OwnerFormSection";
import LivingPlaceUsedForm    from "../LivingPlaceUsedForm";
import ApplicantFormSection   from "../ApplicantFormSection";
import LivingPlaceFormSection from "../LivingPlaceFormSection";
import "./CreateRequestForm.scss";

const CreateRequestForm = ({
	delegations : {
		hookForm,
		inptsRules,
		isEditing,
		isLine3Form,
		placesOptions,
		watcherHousingType,
	},
}) => {
	return (
		<div id="CreateRequestForm">
			<HeaderFormInpts
				hookForm={hookForm}
				isEditing={isEditing}
				isLine3Form={isLine3Form}
			/>
			<hr />
			<ApplicantFormSection
				hookForm={hookForm}
				inptsRules={inptsRules}
			/>
			<OwnerFormSection
				hookForm={hookForm}
				inptsRules={inptsRules}
				isLine3Form={isLine3Form}
			/>
			<hr />
			{
				isLine3Form ?
					<Line3Form
						hookForm={hookForm}
						inptsRules={inptsRules}
						placesOptions={placesOptions}
					/>
					:
					<>
						<LivingPlaceFormSection
							hookForm={hookForm}
							inptsRules={inptsRules}
							type={watcherHousingType?.value > 0 ? "Usada" : "Nueva"}
						/>
						{
							watcherHousingType?.value > 0 &&
								<>
									<hr />
									<LivingPlaceUsedForm
										hookForm={hookForm}
										inptsRules={inptsRules}
									/>
								</>
						}
					</>
			}
		</div>
	);
};

export default CreateRequestForm;
